@import url(https://fonts.googleapis.com/css?family=Baloo+Chettan+2&display=swap);
.search-form .input-group-text {
    width: 100%;
}

.search-form.search-form-color .input-group-text {
    background-color: hsl(0, 0%, 100%);
    border-radius: 8px;
    border: 0;
    color: rgb(174, 146, 240);
    padding: 20px;
    box-shadow: 0 0 9px 2px rgba(19, 25, 31, 0.2);
}


.search-form.search-form-color .form-control {
    color: rgb(42, 41, 41);
    font-weight: bold;
    font-size: 20px;
    background-color: transparent;
    padding: 0 20px;
}

.form-control::-webkit-input-placeholder {
    color: rgb(182, 182, 182);
}

.form-control:-ms-input-placeholder {
    color: rgb(182, 182, 182);
}

.form-control::-ms-input-placeholder {
    color: rgb(182, 182, 182);
}

.form-control::placeholder {
    color: rgb(182, 182, 182);
}

.search-form.search-form-color .nc-icon {
    font-size: 20px;
}
.card-deck {
    width: 100%;
    margin: 0 auto 40px 0;
}

.title {
    font-weight: bold;
}

.sub-titulo, title {
    font-size: medium;
    font-family: 'Baloo Chettan 2', cursive;
}

.card-text {
    font-size: small;
    font-family: 'Baloo Chettan 2', cursive;
}

.text-ellipsis {
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    height: 90px;
    margin-bottom: 40px;
}

@media screen and (min-width: 768px) {
    .card-deck {
        width: calc(100% / 2);
    }
}

@media screen and (min-width: 900px) {
    .card-deck {
        width: calc(100% / 4);
    }
}


.campo-busca {
    position: relative;
    padding: 100px 50px 150px 50px;
}

.search-highlight {
    padding: 48px 0;
    background-color: #f8f8f8;
}
