.search-form .input-group-text {
    width: 100%;
}

.search-form.search-form-color .input-group-text {
    background-color: hsl(0, 0%, 100%);
    border-radius: 8px;
    border: 0;
    color: rgb(174, 146, 240);
    padding: 20px;
    -webkit-box-shadow: 0 0 9px 2px rgba(19, 25, 31, 0.2);
    box-shadow: 0 0 9px 2px rgba(19, 25, 31, 0.2);
}


.search-form.search-form-color .form-control {
    color: rgb(42, 41, 41);
    font-weight: bold;
    font-size: 20px;
    background-color: transparent;
    padding: 0 20px;
}

.form-control::placeholder {
    color: rgb(182, 182, 182);
}

.search-form.search-form-color .nc-icon {
    font-size: 20px;
}